import { Component, OnInit } from '@angular/core';
import * as jwt_decode from 'jwt-decode';
import * as $ from "jquery";
import { LoadJsService } from '../../services/loadjs/load-js.service';
import { LoginService } from '../../services/login/login.service';

@Component({
  selector: 'app-all-post',
  templateUrl: './all-post.component.html',
  styleUrls: ['./all-post.component.css'],
})
export class AllPostComponent implements OnInit {
  constructor(
    private loadJsService: LoadJsService,
    private loginService: LoginService
  ) {}

  tokens: any;
  status: any;
  status_id: any;
  statusContainer: any[] = [];
  userArray: any[] = [];

  ngOnInit(): void {
    //  LOAD JS 
    this.loadJsService.loadJs();

    //  GET USER ID 
    this.tokens = jwt_decode(localStorage.getItem('user_token'));

    // GET USERS DETAILS
    this.getUserDetails(this.tokens.id)

    // Setup connection 
    this.loginService.setupSocketConnection()

    //  GET ALL STORED STATUS 
    this.getAllStoredStatus();

    //  GET ALL STATUS 
    this.getAllStatus();
  }

  getUserDetails(id) {
    this.loginService.getUserDetails(id).subscribe(data => { this.userArray = data.result; })
  }

  likeStatus(status_id, event) {
    var background = $(event.currentTarget).css('background-color');
    console.log('id :::::', background);
    if (background == 'rgb(255, 94, 58)') {
      // FOR UNLIKE
      document.getElementById('like-color').style.backgroundColor = 'rgb(154, 159, 191)';
      var postData = {
        user_id: this.tokens.id,
        status_id: status_id,
        status: 'unlike',
      };
      this.like(postData);
    } else {
      // FOR LIKE
      document.getElementById('like-color').style.backgroundColor = 'rgb(255, 94, 58)';
      var postData = {
        user_id: this.tokens.id,
        status_id: status_id,
        status: 'like',
      };
      this.like(postData);
    }
  }

  uploadStatus() {
    console.log('status :::::', this.status);
    
    if (this.status) {
      this.loginService.emit('uploadStatus', {
        user_id: this.tokens.id,
        status: this.status,
      });
    }
    this.status = '';
  }

  getAllStatus() {
    this.loginService.listen('status').subscribe((data) => {
      console.log('data :::::', data);
      this.statusContainer = data;
      this.ngOnInit();
    });
  }

  getAllStoredStatus() {
    this.loginService.getAllStatus().subscribe(
      (data) => {
        console.log('all status :::::', data.result);
        
        this.statusContainer = data.result;
      },
      (err) => {
        console.log('error in get status :::::', err);
      }
    );
  }

  like(data) {
    this.loginService.like(data).subscribe(
      (result) => {
        console.log('like result :::::', result);
      },
      (err) => {
        console.log('error in like :::::', err);
      }
    );
  }
}
