import { Component, OnInit } from '@angular/core';
import * as jwt_decode from 'jwt-decode';
import { LoginService } from "../../services/login/login.service";

@Component({
  selector: 'app-education',
  templateUrl: './education.component.html',
  styleUrls: ['./education.component.css']
})
export class EducationComponent implements OnInit {

  constructor(private loginService: LoginService) {}

  tokens: any;
  description: any;
  title: any;
  duration: any;
  objectArray: any[] = [];

  ngOnInit(): void {
    // ======================================== ADD FIELDS ============================= 
    this.add()

    // ===================================== GET DATA FROM TOKEN ========================
    this.tokens = jwt_decode(localStorage.getItem('user_token'));
  }

  add(){
    this.objectArray.push(
      {
        title: '',
        description: '',
        duration: ''
      }
    )
  }

  postEducation(){
    this.objectArray.map(data => {
      if (data.title || data.description || data.duration) {
        var postObject = this.createObject(this.tokens.id, data.description, data.title, data.duration)
        this.putEducation(postObject)
      }
    })
  }

  // =============================== CREATE OBJECT TO POST HOBBIES ==================
  createObject(user_id, description, title, duration) {
    return {
      user_id: user_id,
      description: description,
      title: title,
      duration: duration
    };
  }

  putEducation(data){
    this.loginService.updateEducation(data).subscribe(
      output => {},
      err => {
        console.log('error in education :::::', err);
      }
    )
  }

}
