import { Injectable } from '@angular/core';
import { BehaviorSubject } from "rxjs";

@Injectable({
  providedIn: 'root'
})
export class OpenPlaylistService {

  constructor() { }

  private statusService = new BehaviorSubject<boolean>(false);
  public share = this.statusService.asObservable()
  
  updatedata(status) {
    this.statusService.next(status)
  }

  private tabService = new BehaviorSubject<boolean>(false);
  public sharable = this.tabService.asObservable()
  
  updatetab(status) {
    this.tabService.next(status)
  }

  private chatShowService = new BehaviorSubject<boolean>(false);
  public chatSharable = this.chatShowService.asObservable()

  private personalShowService = new BehaviorSubject<boolean>(false);
  public personalSharable = this.personalShowService.asObservable()

  private accountService = new BehaviorSubject<boolean>(false);
  public accountSharable = this.accountService.asObservable()


  private hobbiesService = new BehaviorSubject<boolean>(false);
  public hobbiesSharable = this.hobbiesService.asObservable()

  private educationService = new BehaviorSubject<boolean>(false);
  public educationSharable = this.educationService.asObservable()

  setValueForDisplay(status, data){
    if (data == "Personal_info") {
      this.setValue(false, false, status, false, false)
    } 
    if (data == "Account_setting") {
      this.setValue(status, false, false, false, false)
    }
    if (data == "chat") {
      this.setValue(false, status, false, false, false)
    }
    if (data == "hobbies") {
      this.setValue(false, false, false, status, false)
    }
    if (data == "education") {
      this.setValue(false, false, false, false, status)
    }
  }

  setValue(value1, value2, value3, value4, value5){
    this.accountService.next(value1)
    this.chatShowService.next(value2)
    this.personalShowService.next(value3)
    this.hobbiesService.next(value4)
    this.educationService.next(value5)
  }
}
