import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LoginService } from '../../services/login/login.service';
import { LoadJsService } from '../../services/loadjs/load-js.service';

@Component({
  selector: 'app-landing-page',
  templateUrl: './landing-page.component.html',
  styleUrls: ['./landing-page.component.css'],
})
export class LandingPageComponent implements OnInit {
  constructor(
    private loginServices: LoginService,
    private router: Router,
    private route: ActivatedRoute,
    private loadJsService: LoadJsService
  ) {
    this.loadJsService.loadJs();
  }

  first_name: any;
  last_name: any;
  datetimepicker: any;
  email: any;
  password: any;
  gender: any = 'MA';
  error: any;
  referral: any;
  optionsCheckboxes: any = false;
  public showDiv: boolean = false;

  datePicker(event) {
    this.datetimepicker = event.currentTarget.value;
  }

  selectGender(gender) {
    this.gender = gender.currentTarget.value;
  }

  login() {
    const newData = {
      email: this.email,
      password: this.password,
    };

    this.loginServices.updatedata(newData.email);

    this.loginServices.login(newData).subscribe(
      (data) => {
        console.log('data of login >>>>>>', data);
        if (data.code == 200) {
          localStorage.setItem('user_token', data.result);
          this.loginServices.updateLoginService(data.data);
          this.router.navigate(['/home']);
        } else {
          this.showDiv = true;
        }
      },
      (err) => {
        this.showDiv = true;
      }
    );
  }

  register() {
    const registerData = {
      first_name: this.first_name,
      last_name: this.last_name,
      email: this.email,
      dob: this.datetimepicker,
      gender: this.gender,
      password: this.password,
    };

    var registerFormData = Object.entries(registerData);

    for (let i = 0; i < registerFormData.length; i++) {
      const value = registerFormData[i];
      var length = registerFormData.length;
      console.log('values :::::', value[0]);

      if (value[1] == undefined) {
        this.error = 'Fill ' + value[0];
        this.showDiv = true;
        break;
      }
      if (i == length - 1) {
        if (this.optionsCheckboxes == false) {
          this.error = 'Accept terms and conditions';
          this.showDiv = true;
        }else{
          this.loginServices.register(registerData).subscribe(
            (data) => {
              if (data.code == 200) {
                var dataOfReferral = {
                  user_id: data.result._id,
                  name: data.result.first_name
                }
                console.log('refer transfer :::::', this.referral);
                if (this.referral != null) {
                  var dataOfTransfer = {
                    private_key: dataOfReferral.user_id
                  }
                  this.loginServices.transferToReferral(dataOfTransfer).subscribe(
                    data => {
                      console.log('referral id of friend :::::', data);
                    },
                    err => {
                      console.log('error in transfer of money :::::', err);
                    }
                  )
                }
                this.loginServices.getReferralId(dataOfReferral).subscribe(
                  data => {
                    console.log('data of referral :::::', data);
                  },
                  err => {
                    console.log('error :::::', err);
                  }
                )
                alert("You have been registerd, Now you can login")
              } else {
                this.error = data.error;
                console.log('errors :::::', data);
                this.showDiv = true;
              }
            },
            (err) => {
              this.error = err.error.result;
              console.log('error :::::', this.error);
              this.showDiv = true;
            }
          );
        }
      }
    }
  }

  ngOnInit(): void {
    this.route.queryParams.subscribe(
      params => {
        if (Object.keys(params).length > 0) {
          this.referral = params.id
        } else {
          this.referral = null
        }
        console.log('params :::::', this.referral);
      },
      err => {
        console.log('error in getting req.query :::::', err);
      }
    )
  }
}
