import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { Router } from '@angular/router';
import { DOCUMENT } from "@angular/common";
import { LoginService } from '../../services/login/login.service';
import { LoadJsService } from '../../services/loadjs/load-js.service';
import { ProfileHeaderComponent } from "./profile-header/profile-header.component";
import * as jwt_decode from 'jwt-decode';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css'],
})
export class ProfileComponent implements OnInit {
  @ViewChild(ProfileHeaderComponent, { static: false }) header: ProfileHeaderComponent;
  
  constructor(
    private loginService: LoginService,
    private loadJsService: LoadJsService,
    @Inject(DOCUMENT) private document: Document
  ) {}

  referral_link: any;
  tokens: any;
  image_data: any;
  headerFunction: any;

  fileChange(event) {
    this.image_data = <File>event.target.files[0];
    const fd = new FormData();
    fd.append('image', this.image_data, this.image_data.name);
    this.loginService.upload_profile(fd, this.tokens.id).subscribe(
      (data) => {
        this.document.location.reload()
      },
      (err) => {
        console.log('error in uploading profile :::::', err);
      }
    );
  }

  fileChangeOfHeader(event) {
    this.image_data = <File>event.target.files[0];
    const fd = new FormData();
    fd.append('image', this.image_data, this.image_data.name);
    this.loginService.upload_header_image(fd, this.tokens.id).subscribe(
      (data) => {
        this.document.location.reload()
      },
      (err) => {
        console.log('error in uploading profile :::::', err);
      }
    );
  }

  loadImage() {
    var clicked = document.getElementById('input-field');
    clicked.click();
  }

  loadHeaderImage() {
    var clicked = document.getElementById('input-header-field');
    clicked.click();
  }

  ngOnInit(): void {
    // ===================================== LOAD JS ===================================
    this.loadJsService.loadJs();

    // ===================================== GET DATA FROM TOKEN ========================
    this.tokens = jwt_decode(localStorage.getItem('user_token'));

    // ================================== GET USER DATA ================================
    this.loginService.getUserDetails(this.tokens.id).subscribe(
      (data) => {
        this.loginService.referralLink(data.result[0].private_key).subscribe(
          (data_of_refer) => {
            console.log('referral link :::::', data_of_refer);
            this.referral_link = data_of_refer.result;
          },
          (error) => {
            console.log('Error in referral link creations:::::', error);
          }
        );
      },
      (err) => {
        console.log('error :::::', err);
      }
    );
  }
}
