import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import * as jwt_decode from 'jwt-decode';
import { Router } from '@angular/router';
import { OpenPlaylistService } from '../../services/open-playlist.service';
import { LoadJsService } from '../../services/loadjs/load-js.service';
import { LoginService } from '../../services/login/login.service';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css'],
})
export class SidebarComponent implements OnInit {
  constructor(
    private openPlaylistService: OpenPlaylistService,
    private loadJsService: LoadJsService,
    private router: Router,
    private loginService: LoginService
  ) {}

  hasClicked: any = false;
  statusOfSongs: any = false;
  tokens: any;
  userArray: any[] = [];

  getStatus(statusData) {
    this.openPlaylistService.updatedata(statusData);
  }

  showSideBars() {
    if (this.hasClicked) {
      this.hasClicked = false;
      var element = document.querySelector('.fixed-sidebar.open');
      element.classList.remove('open');
    } else {
      this.hasClicked = true;
    }
  }

  showSideBar() {
    if (this.hasClicked) {
      this.hasClicked = false;
      var element = document.querySelector('.fixed-sidebar.open');
      element.classList.remove('open');
    } else {
      this.hasClicked = true;
    }
  }

  logout() {
    localStorage.setItem('user_token', null);
    this.router.navigate(['/login']);
  }

  ngOnInit(): void {
    // ================================= LOAD JS =================================
    this.loadJsService.loadJs();

    // ===================================== GET DATA FROM TOKEN ========================
    this.tokens = jwt_decode(localStorage.getItem('user_token'));

    // ================================== GET USER DATA ================================
    this.loadUsersData(this.tokens.id);

    // ===============================================================================
    this.openPlaylistService.share.subscribe(
      (status) => {
        this.statusOfSongs = status;
      },
      (err) => {
        console.log('error in status of songs :::::', err);
      }
    );
  }

  loadUsersData(id) {
    this.loginService.getUserDetails(id).subscribe(
      (data) => {
        this.userArray = data.result;
      },
      (err) => {
        console.log('error :::::', err);
      }
    );
  }
}
