import {
  Component,
  OnInit,
  Inject,
  AfterViewInit,
  ViewChild,
  ElementRef,
  Input,
} from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { ImageCroppedEvent } from 'ngx-image-cropper';
import { LoginService } from '../../services/login/login.service';
import { LoadJsService } from '../../services/loadjs/load-js.service';
import * as jwt_decode from 'jwt-decode';

@Component({
  selector: 'app-image-crop',
  templateUrl: './image-crop.component.html',
  styleUrls: ['./image-crop.component.css'],
})
export class ImageCropComponent implements OnInit {
  constructor(
    private loginService: LoginService,
    private loadJsService: LoadJsService,
    @Inject(DOCUMENT) private document: Document
  ) {
    this.imageDestination = '';
  }

  @ViewChild('image', { static: false })
  public imageElement: ElementRef;
  public imageDestination: string;

  tokens: any;
  image_data: any;
  headerFunction: any;
  fileToUpload: File;
  imageChangedEvent: any = '';
  croppedImage: any = '';

  // convert image to uri to blob
  dataURItoBlob(dataURI): Blob {
    const byteString = atob(dataURI.split(',')[1]);
    const mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];
    const ab = new ArrayBuffer(byteString.length);
    let ia = new Uint8Array(ab);
    for (let i = 0; i < byteString.length; i++) {
      ia[i] = byteString.charCodeAt(i);
    }
    return new Blob([ab], { type: mimeString });
  }

  imageCropped(event: ImageCroppedEvent) {
    this.croppedImage = event.base64;
  }

  imageLoaded() {
    // show cropper
  }
  cropperReady() {
    // cropper ready
  }
  loadImageFailed() {
    // show message
  }

  fileChange(event) {
    this.imageChangedEvent = event;
  }

  uploadAttachmentToServer() {
    var fileToUpload = new File([this.dataURItoBlob(this.croppedImage)], 'filename.png');
    const fd = new FormData();
    fd.append('image', fileToUpload, fileToUpload.name);
    this.loginService.upload_profile(fd, this.tokens.id).subscribe(
      (data) => {
        this.document.location.reload();
      },
      (err) => {
        console.log('error in uploading profile :::::', err);
      }
    );
  }

  fileChangeOfHeader() {
    var fileToUpload = new File([this.dataURItoBlob(this.croppedImage)], 'filename.png');
    const fd = new FormData();
    fd.append('image', fileToUpload, fileToUpload.name);
    this.loginService.upload_header_image(fd, this.tokens.id).subscribe(
      (data) => {
        this.document.location.reload();
      },
      (err) => {
        console.log('error in uploading profile :::::', err);
      }
    );
  }

  loadImage() {
    var clicked = document.getElementById('input-image-field');
    clicked.click();
  }

  loadHeaderImage() {
    var clicked = document.getElementById('input-header-field');
    clicked.click();
  }

  ngOnInit(): void {
    // ===================================== GET DATA FROM TOKEN ========================
    this.tokens = jwt_decode(localStorage.getItem('user_token'));
  }
}
