import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SidebarComponent } from './components/sidebar/sidebar.component';
import { RightSidebarComponent } from './components/right-sidebar/right-sidebar.component';
import { HomePageComponent } from './components/home-page/home-page.component';
import { MainFrameComponent } from './main-frame/main-frame.component';
import { NewsfeedComponent } from './components/newsfeed/newsfeed.component';
import { FavoriteComponent } from './components/favorite/favorite.component';
import { FriendGroupComponent } from './components/friend-group/friend-group.component';
import { MusicplayerComponent } from './components/musicplayer/musicplayer.component';
import { WeatherComponent } from './components/weather/weather.component';
import { CalenderComponent } from './components/calender/calender.component';
import { CommunityComponent } from './components/community/community.component';
import { BirthdayComponent } from './components/birthday/birthday.component';
import { StatisticsComponent } from './components/statistics/statistics.component';
import { WidgetsComponent } from './components/widgets/widgets.component';
import { AccountComponent } from './components/account/account.component';
import { AuthGuard } from './Auth/auth.gaurd';
import { FriendsListComponent } from './components/friends-list/friends-list.component';


const routes: Routes = [
  {
    path: 'sidebar',
    component: SidebarComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'home',
    component: HomePageComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'rightSideBar',
    component: RightSidebarComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'newsfeed',
    component: NewsfeedComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'favorite',
    component: FavoriteComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'friend',
    component: FriendGroupComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'musicplayer',
    component: MusicplayerComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'weather',
    component: WeatherComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'calender',
    component: CalenderComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'community',
    component: CommunityComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'birthday',
    component: BirthdayComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'stats',
    component: StatisticsComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'widgets',
    component: WidgetsComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'account',
    component: AccountComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'myfriends',
    component: FriendsListComponent
  },
  {
    path: 'login',
    component: MainFrameComponent
  },
  {
    path: 'query_data?id=',
    component: MainFrameComponent
  },
  {
    path: '**',
    component: MainFrameComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
