import { Component, OnInit } from '@angular/core';
import * as jwt_decode from 'jwt-decode';
import { LoadJsService } from '../../services/loadjs/load-js.service';
import { LoginService } from '../../services/login/login.service';

@Component({
  selector: 'app-personal-info',
  templateUrl: './personal-info.component.html',
  styleUrls: ['./personal-info.component.css'],
})
export class PersonalInfoComponent implements OnInit {
  constructor(
    private loadJsService: LoadJsService,
    private loginService: LoginService
  ) {}

  first_name: any;
  last_name: any;
  datetimepicker: any;
  email: any;
  website_link: any;
  phone: any;
  country: any;
  state: any;
  city: any;
  about: any;
  religious: any;
  birthplace: any;
  occupation: any;
  political: any;
  gender: any = 'MA';
  status_type: any = 'Not Married';
  tokens: any;
  userArray: any[] = [];

  datePicker(event) {
    this.datetimepicker = event.currentTarget.value;
  }

  selectGender(gender) {
    this.gender = gender.currentTarget.value;
  }

  selectStatus(status_type) {
    this.status_type = status_type.currentTarget.value;
  }

  ngOnInit(): void {
    // =================================== LOAD JS ===================================
    this.loadJsService.loadJs();

    // ===================================== GET DATA FROM TOKEN ========================
    this.tokens = jwt_decode(localStorage.getItem('user_token'));

    // ================================== GET USER DATA ================================
    this.getUserDetails(this.tokens.id)
  }

  getUserDetails(id) {
    this.loginService.getUserDetails(id).subscribe(
      (data) => {
        this.email = data.result[0].email
        this.first_name = data.result[0].first_name;
        this.last_name = data.result[0].last_name;
        this.datetimepicker = data.result[0].dob;
        this.website_link = data.result[0].website_link
        this.phone = data.result[0].phone
        this.country = data.result[0].country
        this.state = data.result[0].state
        this.city = data.result[0].city
        this.about = data.result[0].about
        this.religious = data.result[0].religious_belief
        this.birthplace = data.result[0].birthplace
        this.occupation = data.result[0].occupation
        this.political = data.result[0].political_incline
        this.gender = data.result[0].gender
        this.status_type = data.result[0].status_type
        this.userArray = data.result;
      },
      (err) => {
        console.log('error :::::', err);
      }
    );
  }

  updatePersonalInfo() {
    var postData = {
      first_name: this.first_name,
      last_name: this.last_name,
      email: this.email,
      phone: this.phone,
      dob: this.datetimepicker,
      gender: this.gender,
      about: this.about,
      website_link: this.website_link,
      country: this.country,
      state: this.state,
      city: this.city,
      birthplace: this.birthplace,
      occupation: this.occupation,
      status_type: this.status_type,
      religious_belief: this.religious,
      political_incline: this.political,
    };
    this.loginService.updatePersonalInfo(postData).subscribe(
      data => {
        alert("Successfully Updated")
        this.getUserDetails(this.tokens.id)
      },
      err => {
        console.log('error in update personal info :::::', err);
      }
    )
  }
}
