import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as jwt_decode from 'jwt-decode';
import { OpenPlaylistService } from '../../services/open-playlist.service';
import { LoginService } from '../../services/login/login.service';
import { LoadJsService } from '../../services/loadjs/load-js.service';
import { FriendsService } from '../../services/friends/friends.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
})
export class HeaderComponent implements OnInit {
  constructor(
    private openPlaylistService: OpenPlaylistService,
    private router: Router,
    private loginService: LoginService,
    private loadJsService: LoadJsService,
    private friendsService: FriendsService
  ) {}

  userArray: any[] = [];
  friendsArray: any[] = [];
  friendRequests: any[] = [];
  friend_request_length: any = 0;
  last_chats: any[] = [];

  searchFriend(data) {
    console.log('length :::::', data.target.value.length);

    var postData = {
      name: data.target.value,
    };
    this.friendsService.search(postData).subscribe(
      (data) => {
        this.friendsArray = data.result;
      },
      (err) => {
        console.log('error in friend search :::::', err);
      }
    );
  }

  addFriend(friend_id) {
    var friendData = {
      friend_id: friend_id,
      user_id: jwt_decode(localStorage.getItem('user_token')).id,
    };
    this.friendsService.add_friend(friendData).subscribe(
      (data) => {
        var requestData = {
          _id: friendData.user_id,
        };
        this.friendsService.get_friend_request(requestData).subscribe(
          (data) => {
            var type = Array.isArray(data.result)
            if (type) {
              this.friend_request_length = data.result[0].sender_data.length;
              this.friendRequests = data.result[0].sender_data;
            } else {
              this.friendRequests = [];
              this.friend_request_length = 0;
            }
          },
          (err) => {
            console.log('error in friend request ::::', err);
          }
        );
      },
      (err) => {
        console.log('error :::::', err);
      }
    );
  }

  logout() {
    localStorage.setItem('user_token', null);
    this.router.navigate(['/login']);
  }

  getStatus(statusData, data) {
    this.openPlaylistService.setValueForDisplay(statusData, data);
  }

  ngOnInit(): void {
    // ===================================== GET DATA FROM TOKEN ========================
    var tokens = jwt_decode(localStorage.getItem('user_token'));

    // ================================== GET USER DATA ================================
    this.loginService.getUserDetails(tokens.id).subscribe(
      (data) => {
        this.userArray = data.result;
      },
      (err) => {
        console.log('error :::::', err);
      }
    );
    // =============================== GET FRIEND REQUEST =============================
    var requestData = {
      _id: tokens.id,
    };
    this.friendsService.get_friend_request(requestData).subscribe(
      (data) => {
        this.friend_request_length = data.result[0].sender_data.length;
        this.friendRequests = data.result[0].sender_data;
      },
      (err) => {
        console.log('error in friend request ::::', err);
      }
    );

    // ================================ GET LAST CHATS OF USER ========================
    this.get_last_chat(tokens.id)
  }

  get_last_chat(user_id) {
    this.loginService.get_last_chat(user_id).subscribe(
      data => {
        this.last_chats = data.result;
      },
      err => {
        console.log('error in get chat :::::', err);
      }
    )
  }
}
