import { Component, OnInit } from '@angular/core';
import { OpenPlaylistService } from "../../../services/open-playlist.service";

@Component({
  selector: 'app-songs-list',
  templateUrl: './songs-list.component.html',
  styleUrls: ['./songs-list.component.css']
})
export class SongsListComponent implements OnInit {

  statusOfSongs : any = false;

  constructor(private openPlaylistService: OpenPlaylistService) { }

  getStatus(statusData) {
    this.openPlaylistService.updatedata(statusData)
  }

  ngOnInit(): void {
    this.openPlaylistService.share.subscribe(
      status => {
        this.statusOfSongs = status
      },
      err => {
        console.log('error in status of songs :::::', err);
      }
    )
  }

}
