import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, BehaviorSubject } from 'rxjs';
import { Network } from '../../Domain/domain';

@Injectable({
  providedIn: 'root'
})
export class FriendsService {

  constructor(private httpClient: HttpClient) { }

  search(data): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/searchFriends', data, {
      headers: header,
    });
  }

  send_friend_request(data): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/friendRequest', data, {
      headers: header,
    });
  }

  get_friend_request(data): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/showFriendRequests', data, {
      headers: header,
    });
  }

  add_friend(data): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/addFriend', data, {
      headers: header,
    });
  }

  friend_suggetion(data): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/friendSuggetion', data, {
      headers: header,
    });
  }

}
