import { Component, OnInit } from '@angular/core';
import * as jwt_decode from "jwt-decode";
import { LoginService } from "../../../services/login/login.service";

@Component({
  selector: 'app-profile-right',
  templateUrl: './profile-right.component.html',
  styleUrls: ['./profile-right.component.css']
})
export class ProfileRightComponent implements OnInit {

  constructor(private loginService: LoginService) { }

  userArray: any[] = []
  friend_count: any;

  ngOnInit(): void {
    var tokens = jwt_decode(localStorage.getItem('user_token'))
    this.loginService.getUserDetails(tokens.id).subscribe(
      data => {
        this.friend_count = data.result[0].friends.length
        this.userArray = data.result;
      },
      err => {
        console.log('error :::::', err);
      }
    )
  }

}
