import { Component, OnInit } from '@angular/core';
import * as jwt_decode from 'jwt-decode';
import { LoginService } from '../../services/login/login.service';

@Component({
  selector: 'app-hobbies',
  templateUrl: './hobbies.component.html',
  styleUrls: ['./hobbies.component.css'],
})
export class HobbiesComponent implements OnInit {
  constructor(private loginService: LoginService) {}

  postData: any = {
    hobbies: '',
    favTvShow: '',
    favMovie: '',
    favGames: '',
    favMusic: '',
    favBook: '',
    favWriter: '',
    otherInterest: ''
  };
  tokens: any;

  ngOnInit(): void {
    // ===================================== GET DATA FROM TOKEN ========================
    this.tokens = jwt_decode(localStorage.getItem('user_token'));
  }

  // ================================= POST HOBBIES =======================================
  updateHobbies() {
    var objectArray = Object.entries(this.postData)
    objectArray.map(data => {
      if (data[1]) {
        var postObject = this.createObject(this.tokens.id, data[1])
        this.putHobbies(postObject)
      }
    })
  }

  // =============================== CREATE OBJECT TO POST HOBBIES ==================
  createObject(user_id, description) {
    return {
      user_id: user_id,
      description: description,
    };
  }

  // ============================== CALL HOBBIES API ================================
  putHobbies(data){
    this.loginService.updateHobbies(data).subscribe(
      output => {},
      err => {
        console.log('error in hobbies :::::', err);
      }
    )
  }
}
