import { Component, OnInit, Input } from '@angular/core';
import * as jwt_decode from "jwt-decode";
import { LoginService } from "../../../services/login/login.service";

@Component({
  selector: 'app-profile-header',
  templateUrl: './profile-header.component.html',
  styleUrls: ['./profile-header.component.css']
})
export class ProfileHeaderComponent implements OnInit {

  constructor(private loginService: LoginService) { }

  userArray: any[] = [];
  @Input() clickEvent: any = false;

  

  ngOnInit(): void {
    // get token data
    var tokens = jwt_decode(localStorage.getItem('user_token'))

    // get user details
    this.loginService.getUserDetails(tokens.id).subscribe(
      data => {
        this.userArray = data.result;
      }
    )

    // call click event
    if (this.clickEvent == "true") {
      setTimeout(() => {
        this.clicked()
      }, 3000)
    }
    
  }

  clicked(){
    var clicked = document.getElementById('input-field');
    clicked.click();
  }

}
