import { Component, OnInit } from '@angular/core';
import * as jwt_decode from "jwt-decode";
import { LoginService } from "../../../services/login/login.service";

@Component({
  selector: 'app-friend-search',
  templateUrl: './friend-search.component.html',
  styleUrls: ['./friend-search.component.css']
})
export class FriendSearchComponent implements OnInit {

  constructor(private loginService: LoginService) { }

  userArray: any[] = [];
  friends_count: any;

  ngOnInit(): void {
    var tokens = jwt_decode(localStorage.getItem('user_token'))
    this.loginService.getUserDetails(tokens.id).subscribe(
      data => {
        this.friends_count = data.result[0].friends_details.length
        this.userArray = data.result;
      },
      err => {
        console.log('error :::::', err);
      }
    )
  }

}
