import { Component, OnInit } from '@angular/core';
import * as jwt_decode from "jwt-decode";
import { FriendsService } from "../../../services/friends/friends.service";

@Component({
  selector: 'app-right-body',
  templateUrl: './right-body.component.html',
  styleUrls: ['./right-body.component.css']
})
export class RightBodyComponent implements OnInit {

  constructor(private friendsService: FriendsService) { }

  friend_array: any[] = [];
  tokens: any;

  send_friend_request(friend_id) {
    var post_data = {
      reciever_id: friend_id,
      sender_id: this.tokens.id
    }
    this.friendsService.send_friend_request(post_data).subscribe(
      data => {
        var postData = {
          _id : this.tokens.id
        }
        this.friend_suggestion(postData)
      },
      err => {
        console.log('err :::::', err);
      }
    )
  }

  ngOnInit(): void {
    this.tokens = jwt_decode(localStorage.getItem('user_token'))
    var postData = {
      _id : this.tokens.id
    }
    this.friend_suggestion(postData)
  }

  friend_suggestion(postData) {
    this.friendsService.friend_suggetion(postData).subscribe(
      data => {
        this.friend_array = data.result
      },
      err => {
        console.log('error in :::::', err);
      }
    )
  }
}
