import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, BehaviorSubject } from 'rxjs';
import { environment } from 'src/environments/environment';
import * as io from 'socket.io-client';
import { Network } from '../../Domain/domain';

@Injectable({
  providedIn: 'root',
})
export class LoginService {

  socket;
  
  constructor(private httpClient: HttpClient) {}

  setupSocketConnection() {
    this.socket = io(environment.SOCKET_ENDPOINT)
  }

  socketId(): Observable<any> {
    return new Observable((subscribe) => {
      this.socket.on('connect', () => {
        console.log('socket status :::::', this.socket.connected);
        this.socket.emit('get-id', this.socket.id)
        subscribe.next(this.socket.id);
      });
    });
  }

  getSocketIdArray(): Observable<any> {
    return new Observable((subscribe) => {
      this.socket.on('get-id', (data) => {
        subscribe.next(data)
      })
    });
  }

  listen(eventname: string): Observable<any> {
    return new Observable((subscribe) => {
      this.socket.on(eventname, (data) => {
        subscribe.next(data);
      });
    });
  }

  emit(eventname: string, data: any) {
    this.socket.emit(eventname, data);
  }

  private statusService = new BehaviorSubject<string>('');
  public share = this.statusService.asObservable();

  private emailService = new BehaviorSubject<string>('hello');
  public sharable = this.emailService.asObservable();

  updatedata(status) {
    this.emailService.next(status);
  }

  private loginServices = new BehaviorSubject<object>({});
  public shareLogin = this.loginServices.asObservable();

  updateLoginService(value){
    this.loginServices.next(value)
  }

  login(newData): Observable<any> {
    console.log('login data >>>>>', newData);
    
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/login', newData, {
      headers: header,
    });
  }

  register(registerData): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/register', registerData, {
      headers: header,
    });
  }

  updatePersonalInfo(postData): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/update_account', postData, {
      headers: header,
    });
  }

  getUserDetails(email): Observable<any> {
    var header = new HttpHeaders();
    return this.httpClient.get(Network + '/start/getUserDetails/' + email, {
      headers: header
    })
  }

  updateSocket(newData): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.put(Network + '/start/updateSocket', newData, {
      headers: header,
    });
  }

  getAllChats(data): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/chat/get_all_chats', data, {
      headers: header,
    });
  }

  getReferralId(data): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/generateprivateKey', data, {
      headers: header,
    });
  }

  transferToReferral(data): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/givePoints', data, {
      headers: header,
    });
  }

  referralLink(referral_id) : Observable<any> {
    var header = new HttpHeaders();
    return this.httpClient.get(Network+ '/link/' + referral_id, {
      headers: header
    })
  }

  upload_profile(file, id): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network+ '/start/uploadprofile/' + id, file, {
      headers: header
    })
  }

  upload_header_image(file, id): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network+ '/start/uploadHeaderImage/' + id, file, {
      headers: header
    })
  }

  get_last_chat(user_id): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.get(Network+ '/chat/get_user_chat/' + user_id, {
      headers: header
    })
  }

  updateHobbies(data): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/addHobbies', data, {
      headers: header,
    });
  }

  updateEducation(data): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/addEducationHIstory', data, {
      headers: header,
    });
  }

  updateEmployment(data): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/addEmploymentHIstory', data, {
      headers: header,
    });
  }

  getAllStatus(): Observable<any> {
    var header = new HttpHeaders();
    return this.httpClient.get(Network+ '/start/getAllStatus', {
      headers: header
    })
  }

  like(data): Observable<any> {
    var header = new HttpHeaders();
    header.append('Content-Type', 'application/json');
    return this.httpClient.post(Network + '/start/like', data, {
      headers: header,
    });
  }

}
