import { Component, OnInit } from '@angular/core';
import { OpenPlaylistService } from "../../services/open-playlist.service";
import { LoadJsService } from "../../services/loadjs/load-js.service";
import { UsersService } from "../../services/users/users.service";
import { FriendsService } from "../../services/friends/friends.service";

@Component({
  selector: 'app-right-sidebar',
  templateUrl: './right-sidebar.component.html',
  styleUrls: ['./right-sidebar.component.css']
})
export class RightSidebarComponent implements OnInit {

  constructor(private openPlaylistService: OpenPlaylistService, private loadJsService: LoadJsService, private usersService: UsersService, private friendsService: FriendsService) {
    // this.loadJsService.loadJs()
  }

  hasClicked: any = false;
  userArray: any[] = [];
  friendArray: any[] = [];
  clickedForSearch: any = false;

  showSideBar() {
    if (this.hasClicked) {
      this.hasClicked = false;
    } else {
      this.hasClicked = true;
    }
  }

  searchFriend(data){
    this.clickedForSearch = true
    var postData = {
      name: data.target.value
    };
    this.getFriendSearch(postData)
  }

  ngOnInit(): void {
    this.getAllUsersData()
  }

  getAllUsersData(){
    this.usersService.getAllUsers().subscribe(
      data => {
        this.userArray = data.result
      },
      err => {
        console.log('error :::::', err);
      }
    )
  }

  getFriendSearch(postData) {
    this.friendsService.search(postData).subscribe(
      data => {
        this.friendArray = data.result
      },
      err => {
        console.log('error in friend search :::::', err);
      }
    )
  }

}
