import { Component, OnInit } from '@angular/core';
import * as jwt_decode from "jwt-decode";
import { LoginService } from "../../../services/login/login.service";

@Component({
  selector: 'app-profile-left',
  templateUrl: './profile-left.component.html',
  styleUrls: ['./profile-left.component.css']
})
export class ProfileLeftComponent implements OnInit {

  constructor(private loginService: LoginService) { }

  userArray: any[] = []
  
  ngOnInit(): void {
    var tokens = jwt_decode(localStorage.getItem('user_token'))
    console.log('token :::::', tokens);
    this.loginService.getUserDetails(tokens.id).subscribe(
      data => {
        this.userArray = data.result;
      },
      err => {
        console.log('error :::::', err);
      }
    )
  }

}
